import React from 'react';
import { graphql, StaticQuery } from 'gatsby';
import styled from '@emotion/styled';
import { css } from '@emotion/core';
import { Card } from '.';
import type { PersonalProjectsQuery } from '../../types/graphql-types';

function getUniqueStr(myStrong?: number): string {
  let strong = 1000;
  if (myStrong) strong = myStrong;
  return new Date().getTime().toString(16) + Math.floor(strong * Math.random()).toString(16);
}

type Props = {
  className?: string;
  // data: PersonalProjectsQuery;
  //   fileName: string;
};

// css
const style = css({
  color: `rgb(34, 41, 47)`,
  display: `flex`,
  justifyContent: `center`,
  alignItems: `center`,
  flexWrap: `wrap`,
  //   flexDirection: `column`,
});

type Node = {
  frontmatter?: {
    title?: string;
    description?: string | null;
    hero?: string | null;
  } | null;
};

type CardsProps = {
  className?: string;
  nodes: Node[];
};

const Cards: React.FC<CardsProps> = (props) => {
  const { className, nodes } = props;
  // css
  const container = css({
    // flex: `1 1 30%`,
    width: `400px`,
  });
  console.log(nodes[0].frontmatter?.hero);

  return (
    <div className={className}>
      <div css={style}>
        {nodes.map((node) => (
          <div key={getUniqueStr()} css={container}>
            <Card
              title={node.frontmatter?.title}
              description={node.frontmatter?.description}
              href={node.frontmatter?.hero!}
            />
          </div>
        ))}
      </div>
    </div>
  );
};

const Component: React.FC<Props> = ({ className }) => (
  <StaticQuery
    query={graphql`
      query PersonalProjects {
        allMdx(filter: { fields: { collection: { eq: "personal-projects" } } }) {
          nodes {
            fields {
              collection
            }
            frontmatter {
              description
              title
              hero
            }
          }
        }
      }
    `}
    render={(data: PersonalProjectsQuery) => (
      <div className={className}>
        <h1>Personal Projects</h1>
        <div></div>
        {data && <Cards nodes={data.allMdx.nodes} />}
      </div>
    )}
  />
);

const StyledComponent = styled(Component)`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  width: 80vw;

  @media screen and (max-width: 1100px) {
  }
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
  }
  @media screen and (max-height: 430px) {
  }
`;

export default StyledComponent;
