import { Link } from 'gatsby';
import React, { useEffect, useState } from 'react';

import styled from '@emotion/styled';
import { Logo } from '../../components';
import ModeButton from './mode-button';

import { useScrollYPosition } from '../../hooks';

import { css } from '@emotion/core';

// css
const h1 = css({
  marginLeft: `180px`,
});

type Props = {
  className?: string;
};

const Component: React.FC<Props> = ({ className }) => {
  const scrollY = useScrollYPosition();

  const [classNames, setClassNames] = useState(className);

  useEffect(() => {
    setClassNames(`${className}${scrollY ? ` active` : ``}`);
  }, [scrollY]);

  return (
    <header className={classNames}>
      <h1 css={h1}>
        <Logo />
      </h1>
      <nav>
        <ul>
          <li>
            <Link to='/'>Top</Link>
          </li>
          <li>
            <Link to='/privacy-policy'>Privacy Policy</Link>
          </li>
          {/* <li>
            <Link to='/about'>About</Link>
          </li>
          <li>
            <Link to='/contact'>Contact</Link>
          </li>
          <li>
            <Link to='/404'>404</Link>
          </li>
          <li>
            <ModeButton />
          </li> */}
        </ul>
      </nav>
    </header>
  );
};

const StyledComponent = styled(Component)`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  width: 100%;
  // background-color: rgba(255, 255, 255, 0.9);
  height: 78px;
  transition: all 500ms ease 0s;

  &.active {
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: rgba(0, 0, 0, 0.12) 0px 4px 8px 0px, rgba(0, 0, 0, 0.08) 0px 2px 4px 0px;
    transition: all 500ms ease 0s;
  }

  > nav {
    padding: 2rem;
    > ul {
      display: flex;
      justify-content: center;
      list-style: none;
      > li {
        > a {
          text-decoration: none;
          text-shadow: 3px 3px 4px #999;
          color: #22292f;
          padding: 2rem;
        }
      }
    }
  }
  @media screen and (max-width: 1100px) {
  }
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
  }
  @media screen and (max-height: 430px) {
  }
`;

export default StyledComponent;
