import React from 'react';
import { AnyImage } from 'src/components';
import { TwitterIcon } from '@ishikawa_masashi/react-icons';
import styled from '@emotion/styled';
import { css } from '@emotion/core';

// css
const style = css({
  color: `rgb(34, 41, 47)`,
  display: `flex`,
  justifyContent: `center`,
  alignItems: `center`,
  flexDirection: `column`,
});

const h2 = css({
  fontSize: `30px`,
});

const h1 = css({
  fontSize: `40px`,
});

const p = css({
  fontSize: `30px`,
});

const twitterIcon = css({
  color: `white`,
  width: `32px`,
  height: `32px`,
});

//this form works only Netlify

type Props = {
  className?: string;
  filename: string;
};

const title: React.FC<{ className?: string }> = ({ className }) => (
  <div className={className}>
    <div css={style}>
      <h2 css={h2}>Hi, Everyone</h2>
      <h1 css={h1}>I'm Masashi.</h1>
      <p css={p}>WEB Developer </p>
      <a css={twitterIcon} href='https://twitter.com/IshikawaMasasi' target='_blank' rel='noopener noreferrer'>
        <TwitterIcon />
      </a>
    </div>
  </div>
);

const StyledTitle = styled(title)`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 640px;
  background-color: rgba(255, 255, 255, 0.4);
  @media screen and (max-width: 1100px) {
  }
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
  }
  @media screen and (max-height: 430px) {
  }
`;

const Component: React.FC<Props> = ({ className, filename }) => (
  <div className={className}>
    <AnyImage filename={filename} />
    <StyledTitle />
  </div>
);

const StyledComponent = styled(Component)`
  overflow: hidden;
  height: 640px;

  & .gatsby-image-wrapper {
    height: 640px;
  }

  @media screen and (max-width: 1100px) {
  }
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
  }
  @media screen and (max-height: 430px) {
  }
`;

export default StyledComponent;
